import angular from 'vendor-bb-angular';

const template = `
  <tr data-ng-if="toggler === item.id" class="bb-table-details">
    <td colspan="{{ colspan }}">
      <div class="row">
        <div class="col-12 col-md-4">
          <strong>{{ 'details.to' | i18n }}</strong>
          <div data-role="details-counter-party-name">
            {{ item.transactionInformation.creditor.name }}</div>
          <div data-role="details-identification">
            {{ item.transactionInformation.creditorAccount.identification.identification }}</div>
          <div data-role="details-counter-party-bank-name">
            {{ item.transactionInformation.creditorAccount.name }}</div>
          <div data-role="details-counter-party-country">
            {{ item.creditAccountIdentification.counterpartyCountry }}</div>
        </div>
        <div class="col-12 col-md-8">
          <div data-ng-if="item.createdBy ">
            <strong>{{ 'details.created.by' | i18n }}</strong>
            <div data-role="details-created-by">
              {{ item.createdBy }} 
              <span data-ng-if="item.requestedExecutionDate">
                {{ 'details.on' | i18n }} {{ item.createdAt | date:dateFormat }}
                {{ 'details.at' | i18n }} {{ item.createdAt | date:'hh:mm' }}
              </span></div>
          </div>
          <br>
          <div class="row">
            <div 
              data-ng-if="item.transactionInformation.endToEndIdentification" 
              class="col-12 col-md-4">
              <strong>{{ 'details.transaction.id' | i18n }}</strong>
              <div data-role="details-payment-reference">
                {{ item.transactionInformation.endToEndIdentification }}
                </div>
            </div>
            <div class="col-12"
              data-ng-class="{
                'col-sm-8': item.transactionInformation.endToEndIdentification, 
                'col-sm-12': !item.transactionInformation.endToEndIdentification}">
              <strong>{{ 'details.description' | i18n }}</strong>
              <div data-role="details-description">
                {{ item.transactionInformation.remittanceInformation.content || '---' }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </td>
  </tr>
`;

const directive = ($compile) => ({
  scope: {
    item: '<',
    toggler: '=',
    template: '<',
    helpers: '<',
    colspan: '<',
  },
  link: ($scope, $el) => {
    const el = $el[0];
    const tr = angular.element($scope.template || template);
    const parentDiv = el.parentNode;
    $compile(tr)($scope);

    parentDiv.insertBefore(tr[0], el.nextSibling);
  },
});

export default directive;



